.qparent {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;

  .outerTextdiv {
    width: 100%;
    max-height: 10%;

    span {
      /* Please answer below questions before starting the surgical simulation. Once you finish 3/3 questions, you may press the “Start” button on the right bottom side of the screen and start the procedure. */

      font-family: "Tomorrow";
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 140%;
      /* or 28px */

      color: #ffffff;
    }
  }

  .innerTextdiv {
    width: 100%;
    height: 90%;
    display: flex;
    justify-content: center;
    align-items: center;

    .qbox {
      width: 90%;
      height: 100%;
      border: 1.02413px solid #88bbef;
      border-radius: 16.3861px;

      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;

      .div0 {
        width: 100%;
        height: 10%;
        display: flex;
        justify-content: flex-end;

        svg {
          color: white;
          width: 2rem;
          height: 2rem;
        }
      }

      .div1 {
        width: 100%;
        height: 20%;
        display: flex;
        justify-content: center;
        align-items: center;

        .headingtxt {
          /* Question 1 */

          font-family: "Tomorrow";
          font-style: normal;
          font-weight: 400;
          font-size: 40px;
          line-height: 140%;
          /* or 56px */
          text-align: center;

          color: #ffffff;
        }
      }
      .div2 {
        width: 90%;
        height: 20%;

        .bodytxt {
          font-family: "Tomorrow";
          font-style: normal;
          font-weight: 400;
          font-size: 20px;
          line-height: 140%;
          /* or 28px */

          color: #ffffff;
        }
      }
      .div3 {
        width: 100%;
        height: 40%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;

        .answer {
          width: 40%;
          height: 40%;
          background: #1a1a1a;
          border-radius: 20px;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          position: relative;
          cursor: pointer;

          span {
            font-weight: 400;
            font-size: 20px;
            color: white;
          }
        }
      }
      .nextQuestionButton {
        width: 100%;
        height: 10%;
        display: flex;
        justify-content: flex-end;

        button {
          width: 200px;
          font-weight: 400;
          font-size: 30px;
          background: #1a1a1a;
          color: white;
          border: 2px solid #627180;
          border-radius: 16.3861px;
          cursor: pointer;
        }
      }
      .remarksButton {
        width: 100%;
        height: 10%;
        display: flex;
        justify-content: flex-end;
      }
    }
  }
}
